import axios from "axios";
import { useEffect, useState } from "react";
import { useAuth } from "../Hooks/authHooks";
import { ArrayChildLoader } from "../GlobalComponents/Loader"
import moment from "moment";
import "moment/locale/fr";


const Agenda = () => {
    moment.locale('fr')
    const { apiInfos } = useAuth()
    const { baseApi, headerApi } = apiInfos;
    const [agendaList, setAgendaList] = useState([]);


    useEffect(() => {
        // moment.locale('fr');
        axios.get(baseApi + '/user/notes', { headers: headerApi })
            .then(res => setAgendaList(res.data))
            .catch(err => console.log(err))

    }, []);

    return <div className="agenda">
        <h1>Votre agenda</h1>
        <section>
            <ArrayChildLoader dataIsLoad={agendaList}>
                {
                    agendaList.map((agd, index) => <div key={"agenda title nb" + index}
                        className="agendaItem">
                        <h2>
                            Pour le  {moment(agd.date).format('Do MMMM YYYY')}
                        </h2>
                        <p>
                            <b>Titre :</b> {agd.title}
                        </p>
                        <p>
                            <b>Statut :</b> {agd.data}
                        </p>
                        <p>
                            <b>Effectu a : </b> {agd.time}
                        </p>
                    </div>)
                }
            </ArrayChildLoader>
        </section>
    </div>
}

export default Agenda;