import { useEffect, useState } from "react";
import moment from "moment";
import axios from "axios";
import { useAuth } from "../Hooks/authHooks";

export const DocumentList = ({ props }) => {
    const { documentList } = props;
    const [finalList, setFinalList] = useState(props.documentList);

    function searchOnArray(initialData, searched, searhKey, resultSetter) {
        const copyData = initialData.slice();
        let filter = '';
        if (searhKey.length == 1) {
            filter = copyData.filter(item => item[searhKey[0]].toLowerCase().includes(searched))
        } else if (searhKey.length == 2) {
            filter = copyData.filter(item => item[searhKey[0]].toLowerCase().includes(searched)
                || item[searhKey[1]].toLowerCase().includes(searched)
            )
        } else if (searhKey.length == 3) {
            filter = copyData.filter(item => item[searhKey[0]].toLowerCase().includes(searched)
                || item[searhKey[1]].toLowerCase().includes(searched)
                || item[searhKey[2]].toLowerCase().includes(searched))
        }
        if (filter.length > 0) {
            resultSetter(filter)
        }
    }

    useEffect(() => {
        setFinalList(props.documentList)
    }, [props])
    return <section className="ffilesList">
        <section className='fSearch'>
            <input type="search" placeholder='Rechercher un document ici...'
                onChange={(event) => {
                    searchOnArray(documentList,
                        event.target.value.toLowerCase(), ['title'], setFinalList)
                }} />
        </section>
        <table>
            <thead>
                <tr>
                    <th>*</th>
                    <th>Titre du doc</th>
                    <th>Date d'ajout</th>
                </tr>
            </thead>

            <tbody>
                {
                    finalList.map((item, index) => <tr key={"doc nb" + index}>
                        <td>{index}</td>
                        <td>
                            <a href={item.file} target="_blank"> {item.title}</a>
                        </td>
                        <td>{moment(item.created_at).format("Do/MMMM/YYYY, h:mm")}</td>
                    </tr>)
                }
            </tbody>
        </table>

    </section>
}

function insertAfter(referenceNode, newNode) {
    referenceNode.parentNode.insertBefore(newNode, referenceNode.nextSibling);
}

function selectFileLabel(inputId, typeFile) {
    const labelList = document.getElementsByTagName('label');
    for (let index = 0; index < labelList.length; index++) {
        if (labelList[index].attributes.for
            && labelList[index].attributes.for.value == inputId) {
            labelList[index].classList.toggle('labelSelected')
            labelList[index].innerHTML = typeFile.split('/')[1].toUpperCase();
            labelList[index].nextElementSibling.style.display = 'block';

        }
    }
}

function closeFileLabel(event, inputId) {
    event.preventDefault();
    event.target.style.display = 'none'

    const labelList = document.getElementsByTagName('label');
    for (let index = 0; index < labelList.length; index++) {
        if (labelList[index].attributes.for
            && labelList[index].attributes.for.value == inputId) {
            labelList[index].classList.toggle('labelSelected')
            labelList[index].innerHTML = 'Choisir';

        }
    }
}

export const AddDocument = ({ props }) => {
    const { agent, authedInfo, apiInfos } = props;
    const { baseApi, headerApi } = apiInfos


    // const {apiInfos} = useAuth();
    const [formValues, setFormValues] = useState({ file: '', title: '' });
    const [addError, setAddError] = useState('');
    const [addSuccess, setAddSuccess] = useState('');

    function handleChange(event) {
        let value = event.target.value;
        let name = event.target.name;
        let inputId = event.target.id;
        let file = event.target.files ? event.target.files[0] : false;

        if (file) {
            let typeFile = file.type;
            selectFileLabel(inputId, typeFile);
            setFormValues({ ...formValues, [name]: file })
        } else {
            setFormValues({ ...formValues, [name]: value })
        }

    }

    function handleSubmit(event) {
        event.preventDefault();
        let value = event.target.value;

        let newdoc = {
            titre: formValues.title,
            file: formValues.file,
            clientId: authedInfo._id,
            userId: agent._id
        }
        let formData = new FormData();
        formData.append("info", JSON.stringify(newdoc));
        formData.append("file", formValues.file);

        // console.log(newdoc)
        axios.post(baseApi + '/user/email', formData, { headers: headerApi })
            .then(res => {
                if (res.data._id) {
                    setAddSuccess('Votre document a bien t ajout,consulter le ici');
                }
            })
            .catch(err => setAddError('Une erreur est surveneue'))
    }


    return <section className='fAddDoc'>
        <h2>Ajouter un document</h2>
        <form onSubmit={(event) => handleSubmit(event)}>
            <div className='formSegment'>
                <label>Expert</label>
                <section>
                    <input type="text" placeholder="Nom de l'expert"
                        value={agent.prenom + ' ' + agent.nom} readOnly />
                </section>
            </div>

            <div className='formSegment'>
                <label>Titre</label>
                <section>
                    <input type="text"
                        placeholder='Ex: Acte de naissance,Carte de sejour,etc...'
                        name="title" onChange={(event) => handleChange(event)} />
                </section>
            </div>

            <div className='formSegment'>
                <label >Fichiers</label>
                <label htmlFor='userDocFile' id='fileLabel'>Choisir</label>
                <button className="fileLabelReset"
                    onClick={(event) => closeFileLabel(event, 'userDocFile')}>Reinitialiser</button>
                <input onChange={(event) => handleChange(event)}
                    type="file" id='userDocFile'
                    name="file" />
            </div>
            <b className="errorField">{addError} </b>
            <b className="successField">{addSuccess} </b>
            <div className='formBtn'>
                <button>Ajouter le document</button>
            </div>

        </form>
    </section>
}