import axios from "axios";
import { useState } from "react";
import { useAuth } from "../Hooks/authHooks";
import { allFields } from "../Raw/fields";
import { handleUserFieldInfoChange } from "../Tools/formFunction";
import { dispatchBtn, dispatchInputOrSelect } from "../Tools/formValidator";
import uniqId from 'uniqid';
export const FormBtn = ({ btn }) => {
    return <div className="formBtn">
        {btn}
    </div>
}

export const UpdateProfilForm = ({ props }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { authedInfo, formNb } = props;

    const ufFields = {
        "form1": [allFields.adresse,
        allFields.codepostal, allFields.password,
        allFields.password_confirmation],
        "form2": [allFields.nom, allFields.prenom,
        allFields.date_de_naissance],
        "form3": [allFields.mail, allFields.telephone]
    };


    // /update-phone
    const [formValues, setFormValues] = useState({
        nom: authedInfo.nom, prenom: authedInfo.prenom,
        date_de_naissance: authedInfo.date_naissance,
        codepostal: authedInfo.codepostal, adresse: authedInfo.address,
        email: authedInfo.email, telephone: authedInfo.telephone,
        currentPassword: '', password: '', password_confirmation: "",
        avatar: "",

        situation_familiale: authedInfo.situation_familiale,
        nom_conjoint: "", prenom_conjoint: "", date_de_naissance_conjoint: ""
    })
    const [upSuccess, setUpSuccess] = useState('')
    const [upError, setUpError] = useState('')

    function handleSubmit(event) {
        event.preventDefault();
        let subApiUrl = '';
        let formData = ''
        if (formNb == "1") {
            subApiUrl = '/user/update-profil'
            formData = new FormData();
            formData.append("info", JSON.stringify({
                "mainPassword": "",
                "address": formValues.adresse,
                "avatar": {},
                "newPassword": formValues.password,
                "codepostal": formValues.codepostal
            }))
            formData.append("avatar", formValues.avatar)
        } else if (formNb == "2") {
            console.log('gen 2')
        } else if (formNb == "3") {
            // console.log('email')
            subApiUrl = '/user/update-phone'
            formData = {
                "email": formValues.email,
                "telephone": formValues.telephone
            }
        } else if (formNb == "4") {

        }

        axios.put(baseApi + subApiUrl, formData, { headers: headerApi })
            .then(res => {
                res.data.nom ? setUpSuccess('Modification enregistr avec success')
                    : setUpError('Une erreur est survenue ,veuillez reesayer')

            })
            .catch(err => setUpError('Une erreur est survenue ,veuillez reesayer'))

    }

    function handleChange(event) {
        handleUserFieldInfoChange(event, formValues, setFormValues, errors, setErrors)
    }


    // const [formNb, setFormNb] = useState()
    const [formBtn, setFormBtn] = useState(dispatchBtn('simple', 'Sauvegarder'))
    const [errors, setErrors] = useState([])

    return <> <div>
        <form onSubmit={(event) => handleSubmit(event)}>
            <h3>{props.formTitle} </h3>
            {
                ufFields["form" + props.formNb].map((field, index) => <div key={"update form" + index}
                    className="formSegment fsFull">
                    <label>
                        {field.label}
                    </label>
                    <section>
                        {dispatchInputOrSelect(formValues, setFormValues, field,
                            handleChange, errors, setErrors)}
                    </section>
                </div>)
            }
            {
                props.formNb == "2" ? <Form2Completion props={{
                    authedInfo,
                    formValues, setFormValues, handleChange,
                    errors, setErrors
                }} />
                    : ''
            }
            <FormBtn btn={formBtn} />
        </form>
    </div>
        {
            formNb == "2" &&
                formValues.situation_familiale === "mari_avec_enfant" ? <AddChildren props={{ authedInfo }} /> : ''
        }
        <b className="successField">{upSuccess} </b>
        <b className="errorField">{upError}</b>
    </>
}



const Form2Completion = ({ props }) => {
    const situationFamilialeField = [
        { value: "clibataire", ph: "Clibataire" },
        { value: "mari_avec_enfant", ph: "Mari avec enfant" },
        { value: "mari_sans_enfant", ph: "Mari sans enfant" },
    ]

    const conjointfields = [
        allFields.nom_conjoint, allFields.prenom_conjoint,
        allFields.date_de_naissance_conjoint
    ]
    const { authedInfo, formValues, setFormValues, handleChange, errors, setErrors } = props;
    return <>
        <div className="formSegment fsFull">
            <label>Situation Familiales</label>
            <section>
                {
                    situationFamilialeField.map((ip, idp) => <label
                        key={"radio nb" + idp}
                        htmlFor={"sf_radio_nb" + idp}>

                        <input type="radio" name="situation_familiale"
                            value={ip.value}
                            id={"sf_radio_nb" + idp}
                            onChange={(event) => handleChange(event)}
                            errmsgname="noErrWithRadio"
                        />
                        <span>{ip.ph}</span>

                    </label>)
                }
            </section>
        </div>
        {
            ['mari_avec_enfant', 'mari_sans_enfant'].
                includes(formValues.situation_familiale) ?
                <>
                    {
                        conjointfields.map((cf, index) => <div className="formSegment fsFull"
                            key={"conjoint field" + index}>
                            <label>{cf.label} </label>
                            <section>
                                {dispatchInputOrSelect(formValues, setFormValues, cf,
                                    handleChange, errors, setErrors)}
                            </section>
                        </div>)
                    }
                </>
                : ''
        }

    </>
}


const AddChildren = ({ props }) => {
    const enfantFields = [allFields.nom_enfant, allFields.prenom_enfant,
    allFields.date_de_naissance_enfant]
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { authedInfo } = props;


    const [currentField, setCurrentField] = useState(false);
    const [childList, setChildList] = useState(authedInfo.enfants)
    const [cFormValues, setCFormValues] = useState({
        nom_enfant: "", prenom_enfant: "",
        date_de_naissance_enfant: ""
    })
    const [upSuccess, setUpSuccess] = useState('')
    const [upError, setUpError] = useState('')


    function handleC(e) {
        const name = e.target.name;
        const value = e.target.value;
        // console.log(name)
        setCFormValues({ ...cFormValues, [name]: value })
    }

    function handleNewChildField(e) {
        e.preventDefault()
        setCurrentField(true)
    }

    function addNewChild(e) {
        e.preventDefault();
        let childCopy = childList;

        if (childCopy.length > 0) {
            childCopy.push({
                id: uniqId(),
                nom: cFormValues.nom_enfant,
                prenom: cFormValues.prenom_enfant,
                date_naissance: cFormValues.date_de_naissance_enfant
            })
        } else {
            childCopy = [
                {
                    id: uniqId(),
                    nom: cFormValues.nom_enfant,
                    prenom: cFormValues.prenom_enfant,
                    date_naissance: cFormValues.date_de_naissance_enfant
                }
            ]
        }
        setCurrentField(false)
        // setChildList(childCopy)

        axios.put(baseApi + '/user/update-enfants', { enfants: childCopy }, { headers: headerApi })
            .then(res => {
                setUpSuccess('Vos Modifications ont bien t enregistrs')
                setChildList(childCopy)
            })
            .catch(err => setUpError('Une erreur est survenue pendant la modification,veuillez reesayer'))

    }

    function ret() {
        if (currentField) {
            return (<>
                {enfantFields.map((ef, index) => <div className="formSegment fsFull"
                    key={"add child field" + index}>
                    <label>{ef.label} </label>
                    <section>
                        <input type={ef.htmlType} placeholder={ef.ph} name={ef.name}
                            onChange={(event) => handleC(event)} />
                    </section>
                </div>)}
                {/* <FormBtn btn={dispatchBtn('simple', 'Sauvegarder cet enfant')} /> */}
                <div className="formBtn">
                    <button type="action"
                        onClick={(event) => addNewChild(event)}>
                        Sauvegarder cet enfant</button>
                </div>
            </>)
        } else {
            return ''
        }
    }

    return <div>
        <form>
            <h3>Consulter/Ajouter/Modifier la liste de vos enfants</h3>
            <section className="upProfilChildList">
                {
                    childList.length > 0 ?
                        childList.map((ch, index) => <div key={"client child nb" + index}>
                            <p>
                                <b>Nom: </b>
                                <span>{ch.nom}</span>
                            </p>
                            <p>
                                <b>Prnom: </b>
                                <span>{ch.prenom}</span>
                            </p>
                            <p>
                                <b>Naissance: </b>
                                <span>{ch.date_naissance}</span>
                            </p>
                        </div>)
                        : ''
                }
            </section>
            {
                !currentField ?
                    <div className="childAdderBtn">
                        <button type="action" onClick={(event) => handleNewChildField(event)}>
                            Ajouter un nouvel enfant</button>
                    </div> : ''
            }
            {ret(currentField)}
            <b className="successField">{upSuccess} </b>
            <b className="errorField">{upError}</b>

        </form>
    </div>
}







export const Form4 = () => {


    const radioArray = [
        { label: 'tudiant(e)', value: 'tudiant(e)' },
        { label: 'Apprenti(e)', value: 'Apprenti(e)' },
        { label: 'Retrait(e)', value: 'Retrait(e)' },
        { label: 'Demandeur demploi', value: 'Demandeur demploi' },
        { label: 'Enceinte', value: 'Enceinte' },
        { label: 'En situation de handicap', value: 'En situation de handicap' },
        { label: 'Inapte au travail', value: 'Inapte au travail' },
        { label: 'Actif (salari / indpendant)', value: 'Actif (salari / indpendant)' },
    ]
    return <div>
        <form className="form4">
            <h3>Modifier votre situation professionelle ou autre situation </h3>

            <div className="formSegment">
                <b>Je suis ....</b>
                {
                    radioArray.map((ip, index) => <label key={'label input nb' + index}
                        htmlFor={"situation" + index}
                        className="radioSelect">
                        <span> {ip.label}</span>
                        <input type="checkbox" name="situation" id={"situation" + index} />
                    </label>)
                }
            </div>
            <FormBtn btn={dispatchBtn('simple', 'Sauvegarder')} />
        </form>
    </div>
}