import { isValidLenght, isValidChar, isValidAgeValue, isValidNumberValue } from "./formValidator";


export const handleUserFieldInfoChange = (event, userInfo, setUserInfo, errors, setErrors) => {
    const name = event.target.name;
    var value = event.target.value;
    const errMsgName = event.target.attributes['errmsgname'].value;

    if (['nom', 'prenom', 'lieu_de_naissance', 'filire_serie', 'ecole', 'nom_entreprise']
        .includes(name)) {
        isValidLenght(errors, setErrors, name, value, 2, 25, 'Votre ' + errMsgName + ' ')
    }
    if (['autre_competence', 'localisation_entreprise', 'description_entreprise']
        .includes(name)) {
        isValidLenght(errors, setErrors, name, value, 10, 500, 'Votre ' + errMsgName + ' ')
    }
    if (name === 'mail') {
        isValidChar('isMail', errors, setErrors, name, value, ' ')
    }
    if (['nom', 'prenom', 'lieu_de_naissance'].includes(name)) {
        isValidChar('isAlpha', errors, setErrors, name, value, 'Votre ' + errMsgName + ' ')
    }
    if (['filire_serie', 'ecole', 'lieu_de_naissance'].includes(name)) {
        isValidChar('isAlphaNumericWithSpace', errors, setErrors, name, value, 'Votre ' + errMsgName + ' ')
    }
    if (['numero_1', 'numero_2', 'salaire_envisag'].includes(name)) {
        isValidChar('isNumeric', errors, setErrors, name, value, 'Votre ' + errMsgName + ' ')
    }
    if (['numero_1', 'numero_2'].includes(name)) {
        isValidLenght(errors, setErrors, name, value, 8, 8, 'Votre ' + errMsgName + ' ')
    }
    if (['salaire_envisag'].includes(name)) {
        isValidLenght(errors, setErrors, name, value, 5, 7, 'Votre ' + errMsgName + ' ')
    }
    if (['date_de_naissance',].includes(name)) {
        isValidAgeValue(errors, setErrors, name, value, 16, 50, 'Votre age  ')
    }
    if (name == 'number') {
        // isValidChar('isNumeric', errors, setErrors, name, value, 'Votre ' + errMsgName + ' ')
        const regex = /^[0-9- ]+$/.test(value);
        if (regex) {
            if (value.length <= 19) {
                if ([4, 9, 14].includes(value.length)) {
                    value = value + " "
                }
            } else {
                value = value.substring(0, value.length - 1)
            }
        } else {
            value = ""
        }
    }
    if (name == 'code_derriere_carte') {
        if (value.length > 3) {
            value = value.substring(0, value.length - 1)
        }
    }
    if (name == 'telephone') {
        if (value.length > 10) {
            value = value.substring(0, value.length - 1)
        } else {
            isValidLenght(errors, setErrors, name, value, 8, 10, 'Votre ' + errMsgName + ' ')
        }
    }
    if (['logo_entreprise', 'photo_1', 'photo_2', 'profil', 'image'].includes(name)) {
        var maxfilesize = 2048 * 1024  // 2 Mb;
        let file = event.target.files[0];
        var filesize = event.target.files[0].size;
        if (file && filesize <= maxfilesize) {
            let imgUrl = URL.createObjectURL(file)
            setTimeout(() => {
                document.getElementById(name + '-preview').src = imgUrl
            }, 500)

            value = file
        } else {
            const errCopy = errors;
            errors.push({ name: name, error: 'Le fichier ne peut exceder  2mb/mo', type: 'isFileValid' })
            setErrors([...errors, errCopy]);
        }

    }

    if (name === 'password') {
        isValidChar('isPassword', errors, setErrors, name, value, 'Votre mot de passe ')
    }
    if (name === 'password_confirmation') {
        isValidChar('isPasswordConfirm', errors, setErrors, name, value,
            'Les mots de passe ', userInfo.password)
    }
    setUserInfo({ ...userInfo, [name]: value })
}

export const dispatchFormData = (formData, targetObject, formValues) => {
    targetObject.map(data => {
        if (['image_url'].includes((data.key))) {
            const key = data.key.split('_');
            formData.append(key[0], formValues[data.nameOnForm])
        } else {
            formData.append(data.key, formValues[data.nameOnForm])
        }
    })
}