import '../Assets/css/contact.css'


const Contact = () => {
    return <div className="contact">
        <section className='cOurInfos'>
            <h2>Pour nous contacter</h2>
            <h4>
                Si vous souhaitez en savoir plus sur France Assist, cest ici !
            </h4>
            <div>
                <p>
                    <i className="mdi mdi-gmail"></i> contact@france-assist.fr
                </p>
                <p>
                    <i className="mdi mdi-map"></i> 36 Avenue General de Gaulle, 69110 SainteFoylsLyon
                </p>
                <p>
                    <i className="mdi mdi-phone"></i> +33 04 65 84 08 48
                </p>
            </div>

        </section>

        <section className='cSendMessageForm'>
            <h2>Dites nous quelque chose !</h2>
            <form>
                <div className="formSegment">
                    <input type="text" placeholder='Pseudo' />
                </div>
                <div className="formSegment">
                    <input type="mail" placeholder='Mail' />
                </div>
                <div className="formSegment">
                    <textarea placeholder="Message...."></textarea>
                </div>
                <div className="formBtn">
                    <button>Soumettre</button>
                </div>
            </form>
        </section>
    </div>
}


export default Contact;